import React, { useState, useEffect } from 'react';
import Panel from '../components/Panel'
import PDFMaker from '../components/PDFMaker';
import CardPreview from '../components/CardPreview'

function BusinessCard() {
    const [details, setDetails] = useState()
    const [saving, isSaving] = useState(false)
    
    const [mounted, setMounted] = useState(false)

    useEffect(()=>{
        if(saving){
            setMounted(true)
        }else{
            setMounted(false)
        }
    }, [saving])

    return (
        <div className="h-screen w-screen bg-darkTheme">
            <div className="flex flex-row justify-around w-screen h-screen">
                <div className="flex flex-row justify-evenly w-4/5 items-center h-screen">
                    <CardPreview details={details}/>
                    <div className="flex flex-col md:py-8 sm:py-0 h-5/6 content-center items-center w-5/12 ">
                        <Panel saveToPDF = {(save) => isSaving(save) } preview={(input) => {setDetails(input);}} />
                        {mounted ? 
                        <PDFMaker saving={saving} details={details} demount = {(val)=>setMounted(!val)} isSaving={(save) => isSaving(save)}/>
                        : ""}
                    </div>
                </div>
            </div>
        </div>

    );
}

export default BusinessCard;