import React, {useEffect, useRef, useState} from 'react'

import Placeholder from '../assets/placeholder_logo.png'


export default function CardPreview({details}) {
    const canvasRef = useRef();
    const imageRef = useRef();
    const udetails = useState(details); 
    useEffect(()=>{
        const ctx = canvasRef.current.getContext('2d');
        
        ctx.fillStyle = "white";
        ctx.fillRect(0, 0, canvasRef.current.width, canvasRef.current.height);
        
    


        ctx.beginPath();
        ctx.moveTo(0,canvasRef.current.height/2-64);
        ctx.lineTo(1000, 128+64);
        ctx.stroke(); 
        if(details){
            var image = new Image();
            image.src = details.image;
            image.addEventListener('load', e => {
                var scale = Math.min(canvasRef.current.width / image.width, canvasRef.current.height / image.height)-4;
                // get the top left position of the image
                var x = (canvasRef.current.width / 2) - (canvasRef.current.width / 2) * scale;
                var y = (canvasRef.current.height / 2) - (image.height / 2) * scale;
                ctx.drawImage(image, canvasRef.current.width/2-128, y-128-16, image.width * scale, image.height * scale);
              });
            console.log(details.image)
            ctx.fillStyle=details.titleColor
            ctx.font = "600 28pt Arial";
            ctx.fillText(details.documentTitle, 256+64,128);
            ctx.fillStyle="black"
            ctx.font = "700 18pt Arial";
            let y_pos = 128*2+64;
            let x_pos = 64+128*3+64;
            ctx.fillText(details.firstName +" "+ details.lastName, 64, 128*2+64);
            ctx.font = "400 18pt Arial";
            ctx.fillText(details.telephone, 64, 128*2+64+64);
            ctx.fillText("xxxx", 64, 128*2+64+64+32);
            ctx.font = "400 18pt Arial";
            ctx.fillText(details.addressLine1, x_pos, y_pos+=32);
            if(details.addressLine2)ctx.fillText(details.addressLine2, x_pos, y_pos+=32);
            ctx.fillText(details.city, x_pos, y_pos+=32);
            ctx.fillText(details.postalCode, x_pos, y_pos+=32);

        }
    },[details])

    return (
        <>
            <canvas ref={canvasRef} width={768} height={512}>
            </canvas>
            <img  ref={imageRef} src={Placeholder} style={{display:"none"}}/>
        </>
        // <div className="bg-white w-3/5 h-3/6 m-8 flex center">
        //     <div>
        //         <img src={details.img} alt="company logo"></img>
        //     </div>

        //     <div>
        //         <span>{details.firstName}</span>
        //         <span>{details.lastName}</span>
        //     </div>
        //     <div>
        //         <span>{details.jobTitle}</span> 
        //     </div>
        //     <div className="flex flex-row justify-between">
        //         <div>
        //             <p>xxxx</p>
        //             <p>{details.phoneNumber}</p>
        //         </div>

        //         <div>
        //             <p>{details.addressLine1}</p>
        //             {details.addressLine2 ? <p>{details.addressLine1}</p> : ""}
        //             <p>{details.city}</p>
        //             <p>{details.country}</p>
        //             <p>{details.postalCode}</p>
        //         </div>
        //     </div>

        // </div>
    )
}
