import React, {useState} from 'react'
import { SketchPicker } from 'react-color';

export default function Details({setDetails}) {
    // Reuses the same props for each input field
    const addresses = [
        { 
            locationName:"United Kingdom, Liverpool",
            addressLine1: "12 Road",
            addressLine2: "Village",
            city: "Liverpool",
            postalCode: "L23 123",
            country: "United Kingdom",
            telephone: "0151 2312023233",
        },
        {
            locationName:"Ireland, Dublin",
            addressLine1: "42 Avenue",
            addressLine2: null,
            city: "Dublin",
            postalCode: "D01 F5P2",
            country: "Ireland",
            telephone: "353 127319283",
        },
        {
            locationName:"United States, New York",
            addressLine1: "32 N.Brewery Street",
            addressLine2: "Bronx",
            city: "New York",
            postalCode: "NY 11717",
            country: "United States",
            telephone: "631-554-5101",
        }
    ]

    const [currentLocation, setCurrentLocation] = useState(addresses[0]) 
    const [colourValue, setColourValue] = useState() 
    const [colorPickerVisible, isColorPickerVisble] = useState(false)
    const locationUpdateHandler = (location) => {
        setCurrentLocation(location)
        setDetails(location)
    }

    const propBuilder = ({ placeholder, id, type }) => {
        const onBlurProp = (e) => { e.target.placeholder = placeholder }
        const onFocusProp = (e) => { e.target.placeholder = '' }
        return ({
            className: "border-solid border-b-2 border-customGrey-900 text-white w-full min-w-full p-2 text-xl rounded bg-darkTheme-400",
            onBlur: onBlurProp,
            onFocus: onFocusProp,
            placeholder,
            id,
            name: id,
            type,
            onChange:(e)=>setDetails({[e.target.name]:e.target.value})

        })
    }
    const handleChangeComplete = (color) => {
        setColourValue(color.hex);
        setDetails({titleColor:color.hex})
        console.log(color)
      };
      
    // Input consistent style without duplicating html
    const inputBuilder = (title, props = "p-0") => {
        return (
            <div className={`flex flex-col mt-2 pr-2 `}>
                <label className="text-sm text-gray-200 font-bold pb-2">{title}</label>
                <input
                    {...propBuilder(props)}
                />
            </div>
        )
    }

    return (
            <div className="flex flex-col justify-around h-full w-full">
                <div className="flex flex-row items-end">
                    {inputBuilder('Document Title' , {placeholder:"Document Title", id:"documentTitle"})}
                    {colorPickerVisible? 
                    <button className="bg-gradient-to-r from-customBlue to-customCyan rounded-lg h-12 p-2 text-white font-semibold" onClick={()=>isColorPickerVisble(false)}>Close</button>  
                    :<button className="bg-gradient-to-r from-customBlue to-customCyan rounded-lg h-12 p-2 text-white font-semibold" onClick={()=>isColorPickerVisble(true)}>Choose Colour</button>
                    }
                </div>
                   
                    <div className="absolute">
                    {colorPickerVisible?   <SketchPicker
                        color={ colourValue }
                        onChangeComplete={ handleChangeComplete }
                    />
                    :""}
                    </div>
            
                <div className="flex flex-row justify-between">
                    {inputBuilder('First Name', { placeholder: 'John', id: 'firstName', type: 'text' })}
                    {inputBuilder('Last Name', { placeholder: 'Doe', id: 'lastName', type: 'text' })}
                </div>
              
                <select 
                className="text-white w-5/6 mr-2 p-2 text-xl bg-darkTheme-400 rounded border-customGrey-900"
                onChange={(e)=>{locationUpdateHandler(addresses[e.target.value])}}
                >
                    {addresses.map((obj,idx)=><option value={idx}>{obj.locationName}</option>)}
                </select>


            
             

                {/* <label className="border-solid border-b-2 border-customGrey-900 text-white w-full min-w-full p-2 text-xl rounded bg-darkTheme-400">
                    {currentLocation.addressLine1}
                </label>
                <label className="border-solid border-b-2 border-customGrey-900 text-white w-full min-w-full p-2 text-xl rounded bg-darkTheme-400">
                    {currentLocation.addressLine2}
                </label>
                
                <div className="flex flex-row justify-between">
                    <label className="border-solid border-b-2 border-customGrey-900 text-white w-5/6 mr-2 p-2 text-xl rounded bg-darkTheme-400">
                        {currentLocation.city}
                    </label>
                    <label className="border-solid border-b-2 border-customGrey-900 text-white ml-2 w-5/6 p-2 text-xl rounded bg-darkTheme-400">
                        {currentLocation.postalCode}
                    </label>
                </div>

                <div className="flex flex-row justify-between">
                    <label className="border-solid border-b-2 border-customGrey-900 text-white w-5/6 mr-2 p-2 text-xl rounded bg-darkTheme-400">
                        {currentLocation.telephone}
                    </label>
                    <label className="border-solid border-b-2 border-customGrey-900 text-white ml-2 w-5/6 p-2 text-xl rounded bg-darkTheme-400">
                        {currentLocation.country}
                    </label>
                </div> */}
                

            </div>
    )
}
