import React from 'react'
import {BrowserRouter, Link} from 'react-router-dom'
import TemplateWidget from '../components/TemplateWidget'

export default function Landing() {

    const itemStyle = "bg-darkTheme-300 w-64 h-64 rounded-lg m-2"
    return (
        <div className="w-screen h-screen bg-darkTheme">
            <h1 className="mb-32 pt-8 text-center text-4xl text-white font-bold">TEMPLATES</h1>
            <div className=" flex justify-center items-center">
                <div className="flex flex-row w-4/6 h-5/6">
                    <Link to="/card">
                        <TemplateWidget title={"Business Cards"}/>             
                    </Link>
                    <Link to="/leaflet">
                        <TemplateWidget title={"Leaflet"}/>             
                    </Link>
                </div>   
            </div>
        </div>
    )
}
